<template>
  <div class="user_data">
    <h1>user_data</h1>
    <div id="main" style="width: 600px;height:400px;">

    </div>
  </div>
</template>

<script>
  export default {
    name: "user_data",
    mounted() {
      var echarts = require('echarts');
      var myChart = echarts.init(document.getElementById('main'));
      myChart.setOption({
        title: {
          text: 'ECharts 入门示例'
        },
        tooltip: {},
        xAxis: {
          data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      });
    }
  }
</script>

<style scoped>

</style>



